import React from 'react'

const LoginForm = () => {
  return (
    <div className='auth-form shadow-sm d-flex flex-column'>
      <div className='col d-flex flex-column align-items-center justify-content-center header-loginForm'>
          <h5 className='m-0'>JMU</h5>
          <p>Administration</p>
      </div>
      <div className='mw-100 px-4 d-flex flex-column justify-content-center px-4 login-body'>
        <div className='mb-3'>
            <label className='form-label'>
                :
            </label>
            <input id='login' className='form-control' required />
        </div>
        <div className='mb-3'>    
            <label className='form-label'>
                :
            </label>
            <input id='password' type={'password'} className='form-control' required />
        </div>
      </div>
      <div className='col d-flex align-items-center'>

      </div>
    </div>
  )
}

export default LoginForm
